"use client";
import { useEffect, useState } from "react";
import { auth, db } from "../firebase";
import { collection, query, where, onSnapshot, orderBy, addDoc } from "firebase/firestore";
import { signOut } from "firebase/auth";

export default function ChatApp() {
  const [users, setUsers] = useState([]);
  const [selectedUser, setSelectedUser] = useState(null);
  const [messages, setMessages] = useState([]);
  const [newMessage, setNewMessage] = useState("");
  const [currentScreen, setCurrentScreen] = useState("chat"); // chat, profile, config, voice

  const user = auth.currentUser;

  useEffect(() => {
    if (!user) return;
    const q = query(collection(db, "users"), where("uid", "!=", user.uid));
    const unsub = onSnapshot(q, snapshot => {
      setUsers(snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() })));
    });
    return unsub;
  }, [user]);

  useEffect(() => {
    if (!selectedUser || !user) return;

    const messagesRef = collection(db, "messages");
    const q = query(
      messagesRef,
      where("senderId", "in", [user.uid, selectedUser.uid]),
      orderBy("timestamp", "asc")
    );

    const unsub = onSnapshot(q, snapshot => {
      const msgs = snapshot.docs
        .map(doc => doc.data())
        .filter(msg =>
          (msg.senderId === user.uid && msg.receiverId === selectedUser.uid) ||
          (msg.senderId === selectedUser.uid && msg.receiverId === user.uid)
        );
      setMessages(msgs);
    });

    return unsub;
  }, [selectedUser, user]);

  const handleSendMessage = async () => {
    if (!newMessage.trim() || !selectedUser) return;
    await addDoc(collection(db, "messages"), {
      senderId: user.uid,
      receiverId: selectedUser.uid,
      text: newMessage,
      timestamp: new Date()
    });
    setNewMessage("");
  };

  const handleLogout = () => {
    signOut(auth).then(() => {
      window.location.href = "/login";
    });
  };

  return (
    <div style={{ display: "flex", height: "100vh", fontFamily: "Arial, sans-serif" }}>
      {/* Sidebar */}
      <div style={{ width: "250px", borderRight: "1px solid #ccc", padding: "10px", display: "flex", flexDirection: "column", justifyContent: "space-between" }}>
        <div>
          <div style={{ display: "flex", alignItems: "center", marginBottom: "15px" }}>
            <img src={user?.photoURL || "https://via.placeholder.com/50"} style={{ borderRadius: "50%", marginRight: "10px" }} />
            <div>
              <strong>{user?.displayName}</strong>
              <p style={{ margin: 0, fontSize: "12px" }}>{user?.nametag || "Nametag"}</p>
            </div>
          </div>

          <h3>Usuários</h3>
          <ul style={{ listStyle: "none", padding: 0 }}>
            {users.map(u => (
              <li key={u.uid} onClick={() => { setSelectedUser(u); setCurrentScreen("chat"); }}
                  style={{ padding: "8px", cursor: "pointer", borderRadius: "8px", marginBottom: "5px", background: selectedUser?.uid === u.uid ? "#eee" : "transparent" }}>
                {u.name} ({u.nametag})
              </li>
            ))}
          </ul>
        </div>

        {/* Botões inferiores */}
        <div style={{ display: "flex", flexDirection: "column", gap: "5px", marginTop: "10px" }}>
          <button style={{ borderRadius: "8px", padding: "8px" }} onClick={handleLogout}>Sair da conta</button>
          <button style={{ borderRadius: "8px", padding: "8px" }} onClick={() => setCurrentScreen("config")}>Configurações</button>
          <button style={{ borderRadius: "8px", padding: "8px" }} onClick={() => setCurrentScreen("profile")}>Perfil</button>
          <button style={{ borderRadius: "8px", padding: "8px" }} onClick={() => setCurrentScreen("voice")}>Chat de voz</button>
        </div>
      </div>

      {/* Área principal */}
      <div style={{ flex: 1, display: "flex", flexDirection: "column" }}>
        {currentScreen === "chat" && selectedUser && (
          <>
            <div style={{ flex: 1, padding: "10px", overflowY: "auto", borderBottom: "1px solid #ccc" }}>
              {messages.map((msg, idx) => (
                <div key={idx} style={{
                  textAlign: msg.senderId === user.uid ? "right" : "left",
                  margin: "5px 0",
                  padding: "5px 10px",
                  background: msg.senderId === user.uid ? "#DCF8C6" : "#FFF",
                  display: "inline-block",
                  borderRadius: "10px",
                }}>
                  {msg.text}
                </div>
              ))}
            </div>

            <div style={{ display: "flex", padding: "10px" }}>
              <input style={{ flex: 1, padding: "8px" }} value={newMessage} onChange={e => setNewMessage(e.target.value)}
                     onKeyDown={e => e.key === "Enter" && handleSendMessage()} placeholder="Digite sua mensagem..." />
              <button style={{ marginLeft: "5px", borderRadius: "8px", padding: "8px" }} onClick={handleSendMessage}>Enviar</button>
            </div>
          </>
        )}

        {currentScreen === "profile" && (
          <div style={{ padding: "20px" }}>
            <h2>Perfil</h2>
            <img src={user?.photoURL || "https://via.placeholder.com/80"} style={{ borderRadius: "50%" }} />
            <p><strong>{user?.displayName}</strong></p>
            <p>Nametag: {user?.nametag || "Nenhuma"}</p>
          </div>
        )}

        {currentScreen === "config" && <div style={{ padding: "20px" }}><h2>Configurações</h2></div>}
        {currentScreen === "voice" && <div style={{ padding: "20px" }}><h2>Chat de voz</h2></div>}
      </div>
    </div>
  );
}
